import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

//controlled component because of this.props.onClick, board has full control over square
//function component, only have render fuction
function Square(props) {
    //don't need constructor because square does not keep track of game state
      return (
        //not having a function as onClick will cause the alert to go off every time something is re-rendered
        <button 
          className="square" 
          //onclick - create a click event listener, props gets it from the board class
          //don't need this.props because props is being passed in, also do not need to specify a function because ?
          onClick={props.onClick}
          >
          {props.value}
        </button>
      );
  }
  
  class Board extends React.Component {

    renderSquare(i) {
      return <Square 
              value = {this.props.squares[i]}
              onClick={() => this.props.onClick(i)}
             />;
    }
  
    render() {
      return (
        <div>
          <div className="board-row">
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
          </div>
          <div className="board-row">
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
          </div>
          <div className="board-row">
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
          </div>
        </div>
      );
    }
  }
  
  class Game extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        history: [{
          squares: Array(9).fill(null),
        }],
        stepNumber: 0,
        xIsNext: true,
      };
    }

    handleClick(i) {
      const history = this.state.history.slice(0, this.state.stepNumber + 1);
      const current = history[history.length - 1];
      //creates a copy of the squares array
      const squares = current.squares.slice();
      //if square is already filled or someone one ignore the click
      if (calculateWinner(squares) || squares[i]) {
        return;
      }
      squares[i] = this.state.xIsNext ? 'X' : 'O';
      this.setState({
        //appends to the end of the history array, updates the current squares first
        //concat() does not mutate the original array
        history: history.concat([{
          squares: squares,
        }]),
        stepNumber: history.length,
        xIsNext: !this.state.xIsNext,
      });
    }

    jumpTo(step) {
      this.setState({
        stepNumber: step,
        xIsNext: (step % 2) === 0,
      });
    }

    render() {
      //displays most recent game history
      const history = this.state.history;
      //shows the current step number
      const current = history[this.state.stepNumber];
      const winner = calculateWinner(current.squares);
      
      //its a 2d array so you need 2 parameters, one for which step (index) and one for the current move
      const moves = history.map((step, move) => {
        const desc = move ?
          'Go to move #' + move:
          'Go to game start';
        return (
          <li key={move}>
            <button onClick = {() => this.jumpTo(move)}>
              {desc}
            </button>
          </li>
        );
      });

      let status;
      if (winner) {
        status = 'Winner: ' + winner;
      } else {
        status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
      }

      return (
        <div className="game">
          <div className="game-board">
            <Board 
              squares = {current.squares}
              onClick = {(i) => this.handleClick(i)}
            />
          </div>
          <div className="game-info">
            <div>{status}</div>
            <ol>{moves}</ol>
          </div>
        </div>
      );
    }
  }
  
  // ========================================
  
  ReactDOM.render(
    <Game />,
    document.getElementById('root')
  );
  
  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return squares[a];
      }
    }
    return null;
  }